
import { ref, reactive, computed, onMounted, onBeforeUnmount } from "vue";
import { useStore } from "vuex";
import { key, GetterTypes } from "@/store";
import Statuses from "@/components/parts/Statuses.vue";
import SketchIn from "@/components/parts/SketchIn.vue";
import Settings from "@/components/parts/Settings.vue";
export default {
  name: "ECA",
  components: {
    Statuses,
    SketchIn,
    Settings,
  },
  // eslint-disable-next-line
  setup() {
    const store = useStore(key);
    const eca = ref<HTMLElement | null>(null);
    const header = ref<HTMLElement | null>(null);
    const handleResize = () =>
      eca.value?.style.setProperty(
        "--space-top",
        `${header.value?.clientHeight}px`
      );
    const gen = reactive({
      title: "GEN",
      content: computed(() => store.getters[GetterTypes.Gen]),
    });
    const rule = reactive({
      title: "RULE",
      content: computed(() => store.getters[GetterTypes.RuleNumber]),
    });
    onMounted(() => {
      handleResize();
      window.addEventListener("resize", handleResize);
    });
    onBeforeUnmount(() => window.removeEventListener("resize", handleResize));
    return {
      eca,
      header,
      gen,
      rule,
    };
  },
};
